<template>
  <div id="app">
    <site-header></site-header>

    <main class="container">
      <div id="intro">
        <h1>{{ pageTitle }}</h1>
        <h2 v-if="pageSubtitle">{{ pageSubtitle }}</h2>
        <p v-if="pageDescription">{{ pageDescription }}</p>
      </div>
      <div id="content">
        <router-view></router-view>
      </div>
    </main>

    <modal v-if="modal"></modal>

    <site-footer></site-footer>
  </div>
</template>


<script>
import { mapState } from 'vuex';
import SiteHeader from './components/SiteHeader';
import ControlPanel from './components/ControlPanel';
import Modal from './components/Modal';
import SiteFooter from './components/SiteFooter';

export default {
  name: 'app',
  data() {
    return {
      modal: false,
    };
  },
  components: {
    SiteHeader,
    ControlPanel,
    Modal,
    SiteFooter,
  },
  props: [],
  computed: mapState({
    pageTitle: state => state.page.title,
    pageSubtitle: state => state.page.subtitle,
    pageDescription: state => state.page.description,
  }),
};
</script>


<style lang="scss">
// 1. Your custom variables and variable overwrites.
$global-link-color: #DA7D02;

// 2. Import default variables and available mixins.
@import "../node_modules/uikit/src/scss/variables.scss";
@import "../node_modules/uikit/src/scss/mixins.scss";

// 3. Your custom mixin overwrites.
@mixin hook-card() { color: #000; }

// 4. Import UIkit.
@import "../node_modules/uikit/src/scss/uikit.scss";
</style>



